
.header {
  height: 100%;
  width: 100%;
}
.header .layer-container {
    width: 100%;
    height: 100%;
    background: #29ABE2;
    position: relative;
    -webkit-perspective: 2000px;
            perspective: 2000px;
    overflow: hidden;
}
.header .layer-container .layer {
      height: auto;
      position: absolute;
}
.header .layer-container .layer.background {
        width: 100%;
        bottom: 0%;
}
.header .layer-container .layer.foreground {
        width: 100%;
        min-width: 400px;
        bottom: 0%;
}
.header .layer-container .layer.cloud {
        width: 50%;
        top: 50px;
        max-width: 500px;
}



